import React from "react";
import Grid from "@mui/material/Grid";
import Paper from "@mui/material/Paper";
import Typography from "@mui/material/Typography";
import Dialog from "@mui/material/Dialog";
import IconButton from "@mui/material/IconButton";
import makeStyles from "@mui/styles/makeStyles";
import Close from "@mui/icons-material/Close";
import QRCode from "react-qr-code";
import { Box } from "@mui/material";
import PropTypes from "prop-types";
import { PRI_100 } from "../../theme/colors";
import rsudOldImg from "../../assets/logo_new.png";
import dkiImg from "../../assets/dki.png";
import { stripMR } from "../../helpers/common/utility";
var Barcode = require("react-barcode");
const useStyles = makeStyles(() => ({
  qr: {
    width: "50px",
    height: "50px",
    backgroundColor: "#000",
  },
  cardPaper: {
    padding: "16px",
    // minHeight: "150px",
    width: "auto",
    height: "200px",
    display: "flex",
    alignItems: "end",
    borderRadius: "16px !important",
    backgroundColor: PRI_100 + "!important",
    // backgroundImage: `url(${CHBCImage})`,
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
    backgroundPosition: "center",
    position: "relative"
  },
  root: {
    marginTop: "32px",
  },
}));
const CardPatient = (props) => {
  const { name, code } = props;
  const classes = useStyles();
  const [qrDialog, setQRDialog] = React.useState(false);
  const handleDialog = () => {
    setQRDialog(!qrDialog);
  };
  return (
    <>
      <Dialog open={qrDialog}>
        <Paper sx={{ padding: "16px" }}>
          <Grid container spacing={2}>
            <Grid item xs={12} container justifyContent="end">
              <IconButton onClick={handleDialog}>
                <Close />
              </IconButton>
            </Grid>
            <Grid item xs={12} container justifyContent="center">
              {code && (
                <Barcode
                  value={stripMR(code)}
                  fontSize={14}
                  height={100}
                  width={3}
                />
              )}
            </Grid>
          </Grid>
        </Paper>
      </Dialog>
      <Paper className={classes.cardPaper}>
        <img
          src={rsudOldImg}
          alt=""
          style={{
            width: "50px",
            height: "50px",
            objectFit: "contain",
            position: "absolute",
            top: "1.5vh",
            left: "2vw",
          }}
        />
        <div
          style={{
            position: "absolute",
            top: 0,
            left: "50%",
            transform: "translate(-50%, 0%)",
          }}
        >
          <Typography variant="subtitle2" align="center">
            RSUD CENGKARENG
          </Typography>
          <Typography
            variant="body2"
            style={{ fontSize: "9px" }}
            align="center"
          >
            JL. Bumi Cengkareng Indah No.1, Cengkareng Tim., Kecamatan
            Cengkareng, Kota Jakarta Barat, Daerah Khusus Ibukota Jakarta 11730
          </Typography>
        </div>
        <img
          src={dkiImg}
          alt=""
          style={{
            width: "50px",
            height: "50px",
            position: "absolute",
            objectFit: "contain",
            top: "1.5vh",
            right: "2vw",
          }}
        />
        <Grid container spacing={2}>
          <Grid item xs={12} container justifyContent="center">
            {code && (
              <Box
                // sx={{
                //   padding: "3px",
                //   borderRadius: "3px",
                //   backgroundColor: "#FFF",
                //   display: "inline-block",
                // }}
                onClick={handleDialog}
              >
                {/* <QRCode value={code} size={32} onClick={handleDialog} /> */}
                <Barcode value={stripMR(code)} fontSize={10} height={30} />
              </Box>
            )}
          </Grid>
          {/* <Grid item xs={12} container justifyContent="end">
            <Typography variant="body1">{`${name} - ${code}`}</Typography>
          </Grid> */}
        </Grid>
      </Paper>
    </>
  );
};

CardPatient.propTypes = {
  code: PropTypes.any.isRequired,
  name: PropTypes.string.isRequired,
};

export default CardPatient;
